@charset "utf-8";

/* default */
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,form,fieldset,p,button,iframe,th,td {
  margin: 0;
  padding: 0; 
  box-sizing: border-box;
}
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary {
  display: block;
  margin: 0;
  padding: 0; 
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6,dt {
  font-weight :normal;
}
img,fieldset,iframe,button {
  border: 0 none;
}
li {
  list-style:none;
}
i, em, u, cite {
  font-style:normal;
}
a {
  color:#000; 
  text-decoration:none; 
  outline-offset:-1px;
  box-sizing:border-box;
}
.chrome a, 
.safari a {
  outline-offset: -3px;
}
table {
  width:100%;
  border-collapse: collapse; 
  table-layout:fixed; 
}
legend{
  position: absolute; 
  top: 0; 
  left: 0; 
  width: 0; 
  height: 0; 
  overflow: hidden; 
  visibility: hidden; 
  font-size: 0; 
  line-height: 0;
}
select,input,textarea {
  outline:0;
}
select,input,label {
  vertical-align: middle
}
input {
  -webkit-appearance: none; 
  -moz-appearance: none; 
  appearance: none
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
button{
  border:0 none;
  cursor: pointer;
  background: none;
}
select::-ms-expand { 
  display: none; 
}
select::-moz-focus-inner { 
  border: 0; 
  padding: 0; 
}
select:-moz-focusring { 
  color: transparent; 
  text-shadow: 0 0 0 #000; 
}
html, body {
  height: 100%; 
  font-size: 0; 
  line-height: 0;
}
body {
  width: 100%;
  overflow-x: hidden;
  word-break: keep-all;
  word-wrap: break-word;
}
* {
  font-size: inherit; 
  line-height:1.4em; 
  color:black;
}
*:focus {
  outline: 0;
}